@import "../../style/variables.css";

.start-item {
  position: relative;
  background-repeat: no-repeat !important;
  background-position: top 50px left 0 !important;
  color: white;

  padding-top: 130px;
  .start-item__name {
    font-size: 24.67px;
    font-weight: 700;
    line-height: 30px;
  }

  .start-item__info {
    font-size: 23.55px;
    font-weight: 400;
    line-height: 36.83px;
    margin-bottom: 25px;
  }
  .start-item__button {
    padding: 18px 36px 18px 36px;

    p {
      font-style: normal;
      font-weight: 600;
      font-size: 18.2618px;
      line-height: 22px;
    }
  }
}
.start-item:first-child {
  background: url("/public/media/start-item-number1.png");
}

.start-item:last-child {
  background: url("/public/media/start-item-number2.png");
}

@media screen and (min-width: 1024px) {
  .start-item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: start;
  }
} ;
