@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap");
.input__wrapper {
  position: relative;
  width: 100%;
  height: 55.48px;
  background: #ffffff;
  box-shadow: 0 13px 10px rgba(0, 0, 0, 0.54);
  border-radius: 50px;
  margin-bottom: 38.44px;
}
.input__wrapper .input__wrapper_background {
  position: absolute;
  top: 76.86px;
  left: -62.15px;
}
.input__wrapper .input::placeholder {
  color: #2d2d2d;
}
.input__wrapper .input__button {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 55.48px;
  height: 55.48px;
  top: 0;
  right: 0;
}

.input {
  width: 100%;
  height: 100%;
  z-index: 2;
  padding: 0 55.48px 0 75.31px;
  background: #994927;
  border: none;
  border-radius: 50px;
  color: black;
}

