@import "../../style/variables.css";

.choose-server__list_item {
  position: relative;
  max-height: 330px;
  max-width: 354px;

  background: linear-gradient(180deg, #9c4b28 0%, #4a200e 100.23%);
  box-shadow: inset 0px -8.74611px 0px rgba(0, 0, 0, 0.25);
  border-radius: 56.8497px;

  margin: 1.75px 1.75px 24px;
  box-sizing: content-box;
  overflow: hidden;

  flex: 1 1 600px;
  .choose-server__list_item_header {
    height: 135px;
    background-color: white;
  }

  .choose-server__list_item_footer {
    padding: 22px 33.7px 50.1px;

    h2 {
      font-size: 19.77px;
      font-weight: 400;
      line-height: 24px;
    }
    p {
      font-size: 19.77px;
      font-weight: 700;
      line-height: 33.4px;
    }
  }
}
