@import "../../../style/variables.css";
.button {
  background: linear-gradient(180deg, #9c4b28 0%, #4a200e 100%);
  box-shadow: 0 5.19595px 4.15676px rgba(241, 177, 11, 0.26), inset 0px -5.19595px 0px rgba(0, 0, 0, 0.25);
  border-radius: 51.9595px;
  border: none;
}
.button p {
  background: linear-gradient(180deg, #ffffff 55.73%, #aa3504 100%) !important;
  -webkit-background-clip: text !important;
  -webkit-text-fill-color: transparent !important;
  text-fill-color: transparent !important;
}

